@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@300;400;500;700&display=swap);
#root {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    font-family: 'IBM Plex Sans', sans-serif;

    background: rgb(255,255,255);
    background: linear-gradient(90deg, rgba(245,244,255,0.5) 0%, rgba(238,250,255,0.5) 32%, rgba(204,247,255,0.5) 100%);
}


.card-deck-mobile > .card, .card-deck-tablet > .card {
    margin-bottom: 25px;
}

.card-deck-tablet p {
    font-size: 12px;
}

div.card-deck-tablet {
    margin-right: -5px;
    margin-left: -5px;
}

div.card-deck-tablet > div.card {
    margin-right: 5px;
    margin-left: 5px;
}

.card-deck > .card {
    border-radius: 0;
    box-shadow: 0 0 20px 1px rgba(0,0,0,0.05);
}

.card-deck .card-img-top {
    border-top-left-radius: unset;
    border-top-right-radius: unset;
}

.code-section p {
    margin: 0;
}

div.download-card-footer {
    padding-top: 50px;
    background-color: unset;
    border-top: unset;
}

.card-deck-tablet .card-body, .card-deck-mobile .card-body {
    padding: 0
}

.list-group-item {
    border: none !important;
    min-width:200px;
    min-height:100px;
    background-color: unset !important;
}

.list-group {
    flex-wrap: wrap;
    align-content: center;
    align-items: center;
    justify-content: center;
}

.author-name h5 {
    font-size:20px
}

.author-name p {
    font-size:15px
}

.container code {
    color: unset;
}

p > a {
    margin-left: 5px;
    margin-right: 5px;
}

